@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Store {
  width: 1000px;
}
.material-icons {
  display: block;
  margin-top: 10px;
  margin-right: 10px;
  margin-left: auto;
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
}

.Cards {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.card {
  text-align: center;
  width: 250px;
  height: 250px;
  margin: 20px;
  background-color: rgb(245, 245, 245);
  box-shadow: 0px 5px 5px 5px rgb(199, 195, 195);
  padding: 10px;
}

.name {
  -webkit-text-decoration: solid;
          text-decoration: solid;
  text-transform: uppercase;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.color {
  font-size: smaller;
}

.image {
  display: block;
  max-width: 100%;
  max-height: 70%;
  margin-left: auto;
  margin-right: auto;
}

.price-cart {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  color:rgb(248, 72, 72);
}

.cart {
  cursor: pointer;
  text-transform: uppercase;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-weight: bold;
  font-size: x-small;
  background-color: inherit;
  border: 1px solid rgb(248, 72, 72);;
  outline: none;
  padding: 5px 10px;
  border-radius: 20px;
  color: rgb(248, 72, 72);
  max-height: 30px;
}

.cart:hover {
  color:white;
  background-color: rgb(248, 72, 72);
}

.Cards-list {
  display: block;
  width: 100%;
}

.card-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 80px;
  background-color: rgb(245, 245, 245);
  margin-bottom: 3px;
  padding: 5px 20px;
}

.image-list {
  display: block;
  max-width: 90px;
  max-height: 100%;
}

.name-list {
 margin-top: auto;
 margin-bottom: auto;
 -webkit-text-decoration: solid;
         text-decoration: solid;
 text-transform: uppercase;
 font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.color-list {
  font-size: smaller;
  margin-top: auto;
 margin-bottom: auto;
}

.price-list {
  margin-top: auto;
 margin-bottom: auto;
  color: rgb(248, 72, 72);
}

.card-list > .cart 
{
  margin-top: auto;
  margin-bottom: auto;
}
